*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}
body{
    background-color:#6b588a;
    color:white;
    font-family: 'Raleway', sans-serif;
}

.container{
    height:80%;
    display:flex;
    flex-direction: column;
    flex-basis: 95%;
    align-items: flex-start;
    margin: 1%;
}
.tweets{
    flex:0 0 70%;
}
.profile{

    order:1;
    flex:0 0 30%;
    flex-direction: column wrap;
    justify-content: space-between;
}
.profile-content{
    margin:2%;
    background-color: #635d62;
    padding: 5px;
    color:white;
}
.footer{
    position: fixed;
    text-align: center;
    width:100%;
    bottom: 0;
    background-color: black;
    padding:2%;
    color:white;
}
/* for desktops*/
@media (min-width:801px) {
    .container{
        /* flex:0 0 98%; */
        flex-direction: row;
        /* justify-content: space-between; */
    } 
    .tweets{
        flex:0 0 45%;
    }
    .profile{
        flex:0 0 45%;
    }
}